"use client";

const LOGIN_SUCCESS_TOAST_KEY = "loginSuccessToastState";

export const LOGIN_SUCCESS_MESSAGE = "Login Successful! Welcome back, Admin.";
export const LOGIN_SUCCESS_DURATION = 5000;

type LoginToastSnapshot = {
  message: string;
  expiresAt: number;
};

const isBrowser = () => typeof window !== "undefined";

export const saveLoginToastState = (
  message: string,
  durationMs: number
): void => {
  if (!isBrowser()) return;

  const snapshot: LoginToastSnapshot = {
    message,
    expiresAt: Date.now() + durationMs,
  };

  sessionStorage.setItem(LOGIN_SUCCESS_TOAST_KEY, JSON.stringify(snapshot));
};

export const consumeLoginToastState = (): { message: string; duration: number } | null => {
  if (!isBrowser()) return null;

  const raw = sessionStorage.getItem(LOGIN_SUCCESS_TOAST_KEY);
  if (!raw) return null;

  sessionStorage.removeItem(LOGIN_SUCCESS_TOAST_KEY);

  try {
    const parsed = JSON.parse(raw) as LoginToastSnapshot;
    const remaining = parsed.expiresAt - Date.now();

    if (remaining <= 0) {
      return null;
    }

    return {
      message: parsed.message || LOGIN_SUCCESS_MESSAGE,
      duration: remaining,
    };
  } catch {
    return null;
  }
};

